import React, {  useContext, useEffect,useRef,useState } from "react";
import NoteContextdeposite from "../context/notes/NoteContextdeposite"; 
import noteContextalert from "../context/notes/NoteContextalert";
import { useNavigate  } from 'react-router-dom'; 
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.css';
import BootstrapTable from 'react-bootstrap-table-next';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, {Search} from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';

const DepositeSecondTray = () => {

    const contextdeposite = useContext(NoteContextdeposite);
    const contextalert = useContext(noteContextalert);
    const navigate = useNavigate();

    //----------- use state ----------------------
  const [deposite_r, setdeposite_r] = useState("");
  const [bill_no_r, setbillno_r] = useState(""); 
  const [final_figure_r,setfinalfigure_r] = useState("");
  const [advance_r,setadvance_r] = useState("");
  const [remaining_r, setremaining_r] = useState("");
  //----------------------end-------------------


    useEffect(() => {
      if(localStorage.getItem('token'))
      {
        contextdeposite.getsecondtray(localStorage.getItem("city"));
      }
      else
      {
        navigate("/login");
      }    
        // eslint-disable-next-line
      }, []);


  //------------ use ref
  const ref2 = useRef(null);
  const ref2close = useRef(null);
  //----------- end -------------


  const toggleopensecondtray = (deposite,bill_no,Final_Figure,Advance,Remaining) => {
    ref2.current.click();  
    setdeposite_r(deposite);
    setbillno_r(bill_no);
    setfinalfigure_r(Final_Figure);
    setadvance_r(Advance);
    setremaining_r(Remaining);
    
    };


    const handleclicksecondtray = (e) => {
      e.preventDefault();
      contextdeposite.udepositesecondtray(localStorage.getItem("city"),bill_no_r)
      ref2close.current.click();
      //---------- alert
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      contextalert.alertmsgupdate("Successfully Return Bill","alert alert-success");
      setTimeout(() => {
        contextalert.alertmsgupdate(null,"");
      }, 2000);
      //---------- end

    };


     //----------------- FOR BOOTSTRAP TABLE , PAGINATION---------------------
      
      
 
 const edit = (cell, row) => {
  return (
    <div>
      <i
          className='btn btn-primary btn-sm' 
          onClick={() => {
            toggleopensecondtray(
              row.Deposite,
              row.bill_no,
              row.Final_Figure,
              row.Advance,
              row.Remaining
            );
            //toggleopen(note_data);  2nd option aakhi row pan chale
          }}
        >R</i>
    </div>
) 
 }

    
const columns=[ 
  {
    text: 'Return',
    dataField: 'Edited',
    formatter: edit,
    editable: true,
    headerStyle: { backgroundColor: 'whitesmoke',width:'80px' } 
  },
  {
    text: "Bill No",
    dataField: "bill_no",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Name",
    dataField: "Name",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Mobile No",
    dataField: "Mobile",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Delivery Date",
    dataField: "DC_flag",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Return Date",
    dataField: "repairing_flg",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Item Name",
    dataField: "Item",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Item Code",
    dataField: "Item_Code",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Item Price",
    dataField: "Item_Price",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Total Rs",
    dataField: "Final_Figure",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Advance Rs",
    dataField: "Advance",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Remaining Rs",
    dataField: "Remaining",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Deposite Rs",
    dataField: "Deposite",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "city",
    dataField: "city",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  }
 
];

const defaultSorted = [{
  dataField: 'DC_flag',
  order: 'asc'
}];


//------------pagination property
const pagination = paginationFactory({
  page: 1,
  sizePerPageList: [ {
    text: '5', value: 5
  }, {
    text: '10', value: 10
  },
  {
    text: '25', value: 25
  }, {
    text: '50', value: 50
  } ],
  lastPageText: '>>',
  firstPageText: '<<',
  nextPageText: '>',
  prePageText: '<',
  showTotal: true,
  alwaysShowAllBtns: true,
  // onPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // },
  // onSizePerPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // }
}); 

const { SearchBar, ClearSearchButton } = Search;



  return (
<>
     {/* ----------------- FOR UPDATE ----------------------------- */}
     <button 
     ref={ref2}
     type="button"
     className="btn btn-primary d-none"
     data-toggle="modal"
     data-target="#exampleModal"
   >
     Launch demo modal
   </button>

   <div
     className="modal fade"
     id="exampleModal"
     tabIndex="-1"
     role="dialog"
     aria-labelledby="exampleModalLabel"
     aria-hidden="true"
   >
     <div className="modal-dialog" role="document">
       <div className="modal-content">
         <div className="modal-header">
           <h5 className="modal-title" id="exampleModalLabel">
             Edit
           </h5>
           <button
             type="button"
             className="close"
             data-dismiss="modal"
             aria-label="Close"
           >
             <span aria-hidden="true">&times;</span>
           </button>
         </div>
         <div className="modal-body">
         <div className="mb-3">
                  <label className="form-label"><b>Deposite</b></label>
                  <input
                    type="number"
                    className="form-control"
                    id="item_name"
                    name="item_name"
                    onChange={(e) => setdeposite_r(e.target.value)}
                    required
                    value={deposite_r}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Bill No</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={bill_no_r}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Final Figure</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={final_figure_r}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Advance</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={advance_r}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Remaining</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={remaining_r}
                    disabled
                  />
                </div>
                

         </div>
         <div className="modal-footer">
           <button 
             ref={ref2close}
             type="button"
             className="btn btn-secondary"
             data-dismiss="modal"
           >
             Close
           </button>
           <button  
             type="button"
             className="btn btn-primary" 
             onClick={handleclicksecondtray}
           >
             Return Bill
           </button>
         </div>
       </div>
     </div>
   </div>

   {/* ----------------- UPDATE END ---------------------------- */}




 {/* Card is started */}
 <div className="card" style={{marginTop:"25px"}}>
  <div className="card-header" style={{backgroundColor:"#80808061"}}>
          <h2 className="opacity-75">
          <u><center>          
                <i className="fa fa-empire" style={{marginRight:"10px"}}></i>
                Delivered Rental Records
                <i className="fa fa-empire" style={{marginLeft:"10px"}}></i>
            </center></u>            
            </h2>
  </div>            
  <ul className="list-group list-group-flush">
    <li className="list-group-item" style={{backgroundColor:"#afaaab0d"}}>
{/* Card is over */}

{ 
     <ToolkitProvider
     bootstrap4
     keyField='_id'
     data={contextdeposite.secondtray}
     columns={columns}
     search
     wrapperClasses="table-responsive"
   >
     {
       props => (
         <>
         
          <div style={{textAlign:"right"}}><SearchBar  {...props.searchProps}/> 
        <ClearSearchButton  {...props.searchProps} />
           </div>
           <hr />
           <BootstrapTable
             defaultSorted={defaultSorted}
             pagination={pagination}
             {...props.baseProps}
             noDataIndication= "No data to display"
           />
         </>
       )
     }
   </ToolkitProvider>
   }
 
    {/* <table className="table table-striped">
    <thead>
      <tr>
        <th>R</th> 
        <th>Bill No</th> 
        <th>Name</th>
        <th>Mobile</th>
        <th>Delivery Date</th>
        <th>Return Date</th>
        <th>Item Name</th>
        <th>Item Code</th>
        <th>Item Price</th>
        <th>Total Rs</th>
        <th>Advance Rs</th>
        <th>Remaining Rs</th>
        <th>Deposite Rs</th>
        <th>Branch</th>
        
      </tr>
    </thead>
    <tbody>

       {
        contextdeposite.secondtray.map((item_data2, i) => (
            <tr key={i}>
                 <td>
                  <i
                      className='btn btn-primary btn-sm' 
                      onClick={() => {
                        toggleopensecondtray(
                          item_data2.Deposite,
                          item_data2.bill_no,
                          item_data2.Final_Figure,
                          item_data2.Advance,
                          item_data2.Remaining
                        );
                        //toggleopen(note_data);  2nd option aakhi row pan chale
                      }}
                    >R</i>
                  </td>  
                <td>{item_data2.bill_no}</td>
                <td>{item_data2.Name}</td>
                <td>{item_data2.Mobile}</td>
                <td>{item_data2.DC_flag}</td>
                <td>{item_data2.repairing_flg}</td>
                <td>{item_data2.Item}</td>
                <td>{item_data2.Item_Code}</td>
                <td>{item_data2.Item_Price}</td>
                <td>{item_data2.Final_Figure}</td>
                <td>{item_data2.Advance}</td>
                <td>{item_data2.Remaining}</td>
                <td>{item_data2.Deposite}</td>
                <td>{item_data2.city}</td>
            </tr>
        ))
       }
    </tbody>
    </table> */}

    
  {/* Card code is started */}
  </li>
    </ul>
  </div>
  {/* Card code is started */}
    </>
  )
}

export default DepositeSecondTray
