import { useState } from "react";
import NoteContext from "./NoteContextgbill";
 

const NoteState = (props) => {
  const host = "http://localhost:5000";
  
  const notesinitial = [];
  const [itemcode, setitemcode] = useState(notesinitial);
  const [itemprice, setitemprice] = useState(notesinitial);
  const [add_booking_data, setaddbookdata] = useState([notesinitial]);
 

 
 

  //---------------- for API FUNCTIONALITIES --------------------------

  // ------------- select Item ----------------- 
  const getitemcode = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/generate_bill/fetchitemcode`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      },
      body: JSON.stringify({session_city}),
    });
    const json = await response.json();
    setitemcode(json);
  };
 
// ------------- select Item -----------------  
const getitemprice = async (item_code,session_city) => {
  //API CALL
  const response = await fetch(`${host}/api/generate_bill/fetchitemprice`, {
    method: "POST", // *GET, POST, PUT, DELETE, etc.
    headers: {
      "Content-Type": "application/json",
      "auth-token":localStorage.getItem('token')
    },
    body: JSON.stringify({item_code,session_city}),
  });
  const json = await response.json();
  setitemprice(json);
};



// ------------- select Item -----------------  
const getadd_booking_data = async (session_city) => {
  //API CALL 
  const response = await fetch(`${host}/api/generate_bill/fetchbookingdata`, {
    method: "POST", // *GET, POST, PUT, DELETE, etc.
    headers: {
      "Content-Type": "application/json",
      "auth-token":localStorage.getItem('token')
    },
    body: JSON.stringify({ session_city }), // body data type must match "Content-Type" header
  });
  const json = await response.json();
  setaddbookdata(json);
};



  // Add a Item
  const add_bill = async (delivery_date1, return_date1,item_code,formattedDate_st_dt_dmy,formattedDate_end_dt_dmy,item_price,session_city) => {
    const response = await fetch(`${host}/api/generate_bill/addbill`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem('token')
      },
      body: JSON.stringify({ delivery_date1, return_date1,item_code,formattedDate_st_dt_dmy,formattedDate_end_dt_dmy,item_price,session_city }), // body data type must match "Content-Type" header
    });
    const newdata = await response.json();
    setaddbookdata(add_booking_data.concat(newdata)); 
  };

 
  // Delete a note
  const deletebillitem = async (id)=>{
    //API CALL
    const response = await fetch(`${host}/api/generate_bill/deletebillitem/${id}`, {
     method: "DELETE", // *GET, POST, PUT, DELETE, etc. 
     headers: {
       "Content-Type": "application/json",
       "auth-token": localStorage.getItem('token')
     }
   });
   const json = response.json(); // parses JSON response into native JavaScript objects
   

 
 const newbillitem=add_booking_data.filter((data)=>{return data._id!==id})
 setaddbookdata(newbillitem)

}

 
  return (
    <NoteContext.Provider
      value={{ itemcode,setitemcode,getitemcode,getitemprice,itemprice,setitemprice,add_booking_data,setaddbookdata,add_bill,getadd_booking_data,deletebillitem }}
    >
      {props.children}
    </NoteContext.Provider>
  );
};
export default NoteState;
