import { useState } from "react";
import NoteContext from "./NoteContextItemMaster";
 

const NoteState = (props) => {
  const host = "http://localhost:5000";
 

  //-------------  start code for ABOUT PAGE
  const s1 = {
    name: null,
  };
  const [state, setstate] = useState(s1);
  const alertmsgupdate = (id) => {
    setstate({
      name: id,
    });
  };
  //------------- end
  const notesinitial = [];
  const [items, setitem] = useState(notesinitial);

  const [notes, setnotes] = useState(notesinitial); 
  const [imagecontetnt, setimagecontetnt] = useState(notesinitial);

  //---------------- for API FUNCTIONALITIES --------------------------

  // ------------- select Item ----------------- 
  const getitem = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/item_master/fetchitem`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token')
      },
      body: JSON.stringify({session_city}),
    });
    const json = await response.json();
    setitem(json);
  };

  // Add a Item
  const add_item = async (item_name, item_price,session_city) => {
    const response = await fetch(`${host}/api/item_master/additem`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem('token')
      },
      body: JSON.stringify({ item_name, item_price,session_city }), // body data type must match "Content-Type" header
    });
    const note = await response.json();
    setitem(items.concat(note)); 
  };
  //


  
  // Edit a note
  const edititem=async (id,eitem_name,eitem_price)=>{
    //API CALL
    const response = await fetch(`${host}/api/item_master/updateitem/${id}`, {
      method: "PUT", // *GET, POST, PUT, DELETE, etc. 
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token')
      },
      body: JSON.stringify({eitem_name,eitem_price}) // body data type must match "Content-Type" header
    });
    const json = await response.json(); // parses JSON response into native JavaScript objects
    

    let newitems=JSON.parse(JSON.stringify(items))
    //logic to edit in client
    for (let index = 0; index < newitems.length; index++) {
      const element = newitems[index];
      if(element._id===id)
      {
        newitems[index].item_name=eitem_name;
        newitems[index].price=eitem_price; 
        break;
      }
     
    }    
    setitem(newitems);
    // alertmsgupdate("Note has been updated successfully");
    // setTimeout(() => {
    //   alertmsgupdate(null);
    // }, 2000);
}



  // Delete a note
  const deletitem = async (ids,name) => {
    //API CALL
    const response = await fetch(`${host}/api/item_master/deleteitem`, {
      method: "DELETE", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token')
      },
      body: JSON.stringify({ ids, name }),
    }); 
    
    
       
     const newitems = items.filter((item1) => {
     
      return !ids.includes( item1._id ); 
      //return item1._id !== id;      ///------- For a single value delete

    }); 
    setitem(newitems);
    //getitem(localStorage.getItem('city'));
  }; 
  //------------------ end------------------------

   

  // ------------- select Item ----------------- 
  const getimage = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/item_master/fetchimage`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json"  
      } 
    });
    const json = await response.json();
    setimagecontetnt(json);
  };


  // Add a Item
  const add_image = async (formData) => {
    const response = await fetch(`${host}/api/auth/upload-images`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc. 
      body:( formData ), // body data type must match "Content-Type" header
    });
    const note = await response.json();
    getimage();
    //setimagecontetnt(imagecontetnt.concat(note));
   
  };
  //

  

  return (
    <NoteContext.Provider
      value={{ state,items,setitem ,alertmsgupdate,add_item,getitem,deletitem,edititem,getimage,setimagecontetnt,imagecontetnt,add_image }}
    >
      {props.children}
    </NoteContext.Provider>
  );
};
export default NoteState;
