import { useState } from "react";
import NoteContextaddcusdetail from "./NoteContextaddcusdetail";
 

const NoteState = (props) => {
  const host = "http://localhost:5000";
  
  const notesinitial = [];
  const [mobileno, setmobileno] = useState(notesinitial); 
  const [custdata, setcustdata] = useState(notesinitial); 
  const [sumitemprice, setsumitemprice] = useState(notesinitial); 
 
 

  //---------------- for API FUNCTIONALITIES --------------------------

  // ------------- select Item ----------------- 
  const getcustmob = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/add_customer_detail/fetchcustomermob`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      },
      body: JSON.stringify({session_city}),
    });
    const json = await response.json();
    setmobileno(json);
  };

   // ------------- select Item ----------------- 
   const getcustdata = async (session_city,mobileno) => {
    //API CALL
    const response = await fetch(`${host}/api/add_customer_detail/fetchcustomerdata`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      },
      body: JSON.stringify({session_city,mobileno}),
    });
    const json = await response.json();
    setcustdata(json);
  };
  


  // ------------- select Item ----------------- 
   const gettotalprice = async (session_city,bill_no) => {
    //API CALL
    const response = await fetch(`${host}/api/add_customer_detail/fetchtotalprice`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      } ,
      body: JSON.stringify({session_city,bill_no}),
    });
    const json = await response.json();
    setsumitemprice(json);
  };
  

     // ------------- select Item ----------------- 
     const updatebilltable = async (name,mobile,address,finalfigure,advance,remaining,deposite,session_city,bill_no) => {
      //API CALL
      const response = await fetch(`${host}/api/add_customer_detail/updatebilltable`, {
        method: "PUT", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json",
          "auth-token":localStorage.getItem('token'), 
        },
        body: JSON.stringify({name,mobile,address,finalfigure,advance,remaining,deposite,session_city,bill_no}),
      });
       
    };
  
    



  return (
    <NoteContextaddcusdetail.Provider
      value={{ mobileno,setmobileno,getcustmob,getcustdata,custdata,setcustdata,gettotalprice,sumitemprice,setsumitemprice,updatebilltable }}
    >
      {props.children}
    </NoteContextaddcusdetail.Provider>
  );
};
export default NoteState;
