import { useState } from "react";
import NoteContextCreateUser from "./NoteContextCreateUser";
 

const NoteStateCreateUser = (props) => {
  const host = "http://localhost:5000";
 

  //-------------  start code for ABOUT PAGE
  const s1 = {
    name: null,
  };
  const [state, setstate] = useState(s1);
  const alertmsgupdate = (id) => {
    setstate({
      name: id,
    });
  };
  //------------- end
  const notesinitial = [];
  const [users, setusers] = useState(notesinitial); 

  //---------------- for API FUNCTIONALITIES --------------------------



  // Add a Item
  const add_user = async (name, password,city) => {
    const response = await fetch(`${host}/api/create_user/adduser`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json"  
      },
      body: JSON.stringify({ name, password,city }), // body data type must match "Content-Type" header
    });
    const note = await response.json();
    setusers(users.concat(note)); 
  };
  //

  // ------------- select Item ----------------- 
  const getuser = async () => {
    //API CALL
    const response = await fetch(`${host}/api/create_user/fetchusers`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json"  
      } 
    });
    const json = await response.json();
    setusers(json);
  };
 


  // Delete a note
  const deletuser = async (ids,name) => {
    //API CALL
    const response = await fetch(`${host}/api/create_user/deleteusers`, {
      method: "DELETE", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json" 
      },
      body: JSON.stringify({ ids, name }),
    });  
       
     const newitems = users.filter((item1) => {
     
      return !ids.includes( item1._id ); 
      //return item1._id !== id;      ///------- For a single value delete

    }); 
    setusers(newitems);
    
  }; 
  //------------------ end------------------------

   



  return (
    <NoteContextCreateUser.Provider
      value={{ setusers,users,add_user,getuser,deletuser }}
    >
      {props.children}
    </NoteContextCreateUser.Provider>
  );
};
export default NoteStateCreateUser;
