import React, { useContext, useEffect, useRef, useState } from "react";
import NoteContextStock from "../context/notes/NoteContextStock";
import noteContextalert from "../context/notes/NoteContextalert";
import { useNavigate } from "react-router-dom";
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.css';
import BootstrapTable from 'react-bootstrap-table-next';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, {Search} from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';
import NoteContextItemMaster from "../context/notes/NoteContextItemMaster";
import filterFactory, { textFilter   } from 'react-bootstrap-table2-filter';

const StockData = () => {
  const contextStock = useContext(NoteContextStock);
  const contextalert = useContext(noteContextalert);
  const contextItemMaster = useContext(NoteContextItemMaster); 


  const ref1 = useRef(null);
  const refclose = useRef(null);
  const refdelopen = useRef(null);
  const refdelclose = useRef(null);
  
  //----------------- usestate ------------------------------
  const [eitem_name, seteitemname] = useState("");
  const [eitem_code, seteitemcode] = useState("");
  const [esize, setesize1] = useState("");
  const [ebuyprice, setebuyprice] = useState("");
  const [erentprice, setrentprice] = useState("");
  const [ecolor, setcolor] = useState("");
  const [eid, setid] = useState("");

  

  const navigate = useNavigate();
  //var dataarr = [{"Name":"item_data.Name","Issue_Date":"item_data.Issue_Date"}];
  useEffect(() => {
    if(localStorage.getItem('token'))
    {
      contextStock.getstock(localStorage.getItem("city"));
    }
    else
    {
      navigate("/login");
    } 
    // eslint-disable-next-line
  }, []);

  const handleclick = (e) => {
    navigate("/stock"); // header:location
  };
 

  
  let flgformodalclose=true;
   //------------ DELETE
   const handleclick_delete = () => { 
    
    contextStock.delete_stock(peopleInfo);  
    //---------- alert
    document.body.scrollTop = document.documentElement.scrollTop = 0; 
contextalert.alertmsgupdate("Stock has been deleted successfully","alert alert-danger");
    setTimeout(() => {
    contextalert.alertmsgupdate(null,"");
    }, 2000);
    //---------- end
    
    flgformodalclose=false;
    refdelclose.current.click();
    setPeopleInfo([]);
  
};


   //------------ DELETE
   const handleclick_close = () => {
    //---------- alert
   
    if(flgformodalclose===true)
    {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      contextalert.alertmsgupdate("Cancelled , your data is safe","alert alert-info");
      setTimeout(() => {
       contextalert.alertmsgupdate(null,"");
      }, 2000); 
    }
   //---------- end
};



//-------------------- for edit -----------------------------
const toggleopen = (item_name, item_code, size1,buyprice,rentprice,color,id) => {
  ref1.current.click();
  seteitemname(item_name); 
  seteitemcode(item_code);
  setesize1(size1);
  setebuyprice(buyprice);
  setrentprice(rentprice);
  setcolor(color); 
  setid(id);
};

  const handleclick_update = (e) => {
    e.preventDefault();
    contextStock.editstock(eid, eitem_name, eitem_code,esize,ebuyprice,erentprice,ecolor);
    refclose.current.click();

    document.body.scrollTop = document.documentElement.scrollTop = 0;

   contextalert.alertmsgupdate("Stock has been updated successfully","alert alert-success");
    setTimeout(() => {
     contextalert.alertmsgupdate(null,"");
    }, 2000);
  };

  //------------- for multiple checkboxes------------- 

const [peopleInfo, setPeopleInfo] = useState([]);
const handleChange = (chk_id,isselect) => {
 
  if(isselect)
  {
    
    setPeopleInfo([
      ...peopleInfo,
      chk_id 
    ]);
 
  }
  else
  {
    
    setPeopleInfo(peopleInfo.filter((item1) => { 
        return !chk_id.includes( item1 ); 
        //return item1._id !== id;      ///------- For a single value delete 
      }));   
  }   
};
 
  
const handleChange1 = (isSelect,chk_ids) => {
  // console.log(isSelect)
  // console.log(chk_ids);  
  if(isSelect){
    setPeopleInfo([]);
    var all_ids = chk_ids.map(item => item._id);
    // console.log(all_ids)
    setPeopleInfo(
      ...peopleInfo,
      all_ids 
    ); 
  }
  else
  {
    setPeopleInfo([]);
  }
  
};

    


  
// const addcheckbox = (cell, row) => {
//   return (
//     <div>
//       <input
// 					type="checkbox"
// 					name="languages"  
//           value={row._id}
// 					id="flexCheckDefault"  
//           className="myCheck"
//           onChange={handleChange}
// 				/> 
//     </div>
// ) 
//  }

 const edit = (cell, row) => {
  return (
    <div>
      <i
        className="far fa-edit mx-2" 
        onClick={() => {
          toggleopen(
            row.item_name,
            row.item_code,
            row.size1,
            row.buyprice,
            row.rentprice,
            row.color,
            row._id
          ); 
        }}
      ></i>
    </div>
) 
 }

  const columns=[ 
    {
      text: 'Edit',
      dataField: 'Edited',
      formatter: edit,
      editable: true,
      headerStyle: { backgroundColor: 'whitesmoke',width:'55px' } 
    },
    {
      text: "Item Name",
      dataField: "item_name",
      sort: true,
      editable: false,
      headerStyle: { backgroundColor: 'whitesmoke' },
      filter: textFilter() 
    },
    {
      text: "Item Code",
      dataField: "item_code",
      sort: true,
      editable: false,
      headerStyle: { backgroundColor: 'whitesmoke' } 
     
    },
    {
      text: "Size",
      dataField: "size1",
      sort: true,
      editable: false,
      headerStyle: { backgroundColor: 'whitesmoke' } 
     
    },
    {
      text: "Buying Price",
      dataField: "buyprice",
      sort: true,
      editable: false,
      headerStyle: { backgroundColor: 'whitesmoke' } 
     
    },
    {
      text: "Rental Price",
      dataField: "rentprice",
      sort: true,
      editable: false,
      headerStyle: { backgroundColor: 'whitesmoke' } 
     
    },
    {
      text: "color",
      dataField: "color",
      sort: true,
      editable: false,
      headerStyle: { backgroundColor: 'whitesmoke' } 
    } 
   
  ];


  const defaultSorted = [{
    dataField: 'item_name',
    order: 'desc'
  }];

//------------pagination property
const pagination = paginationFactory({
  page: 1,
  sizePerPageList: [ {
    text: '5', value: 5
  }, {
    text: '10', value: 10
  },
  {
    text: '25', value: 25
  }, {
    text: 'All', value: contextStock.stockdata.length
  } ],
  lastPageText: '>>',
  firstPageText: '<<',
  nextPageText: '>',
  prePageText: '<',
  showTotal: true,
  alwaysShowAllBtns: true,
  // onPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // },
  // onSizePerPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // }
}); 
const { SearchBar, ClearSearchButton } = Search;

  return (
    <div>
             {/* ----------------- FOR DELETE MODAL ----------------------------- */}
            
<button ref={refdelopen}  type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#exampleModalCenter">
  Launch demo modal
</button>
 
<div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
  <div className="modal-dialog modal-dialog-centered" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title" id="exampleModalLongTitle">Delete Record</h5>
        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div className="modal-body">
      Are you sure to delete the selected records?
      </div>
      <div className="modal-footer">
        <button type="button" ref={refdelclose} className="btn btn-secondary" data-dismiss="modal" onClick={handleclick_close}>Close</button>
        <button type="button" className="btn btn-primary" onClick={handleclick_delete}>Delete</button>
      </div>
    </div>
  </div>
</div>

      {/* -----------------  END ---------------------------- */}

      
      {/* ----------------- FOR UPDATE ----------------------------- */}
      <button
        ref={ref1}
        type="button"
        className="btn btn-primary d-none"
        data-toggle="modal"
        data-target="#exampleModal"
      >
        Launch demo modal
      </button>

      <div
        className="modal fade"
        id="exampleModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Edit Record
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <form>
                <div className="mb-3">
                  <label className="form-label">Item Name</label>
                  {/* <input
                    type="text"
                    className="form-control"
                    id="item_name"
                    name="item_name"
                    onChange={(e) => seteitemname(e.target.value)}
                    required
                    value={eitem_name}
                  /> */}
                    <input list='browsers' name='item' id='item' className='form-control' value={eitem_name} onChange={(e) => seteitemname(e.target.value)} required></input>
              <datalist id='browsers'>

            {contextItemMaster.items.map((item_data, i) => ( 
                  <option key={i} value={item_data.item_name}>{item_data.item_name}</option>  
            ))} 
               </datalist>
                </div>
                <div className="mb-3">
                  <label className="form-label">Item Code</label>
                  <input
                    type="text"
                    className="form-control"
                    id="item_code"
                    name="item_code"
                    onChange={(e) => seteitemcode(e.target.value)}
                    required
                    value={eitem_code}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">size</label>
                  <input
                    type="number"
                    className="form-control"
                    id="size"
                    name="size"
                    onChange={(e) => setesize1(e.target.value)}
                    required
                    value={esize}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Buying Price</label>
                  <input
                    type="number"
                    className="form-control"
                    id="buyprice"
                    name="buyprice"
                    onChange={(e) => setebuyprice(e.target.value)}
                    required
                    value={ebuyprice}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Rental Price</label>
                  <input
                    type="number"
                    className="form-control"
                    id="rentprice"
                    name="rentprice"
                    onChange={(e) => setrentprice(e.target.value)}
                    required
                    value={erentprice}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Color</label>
                  <input
                    type="text"
                    className="form-control"
                    id="color"
                    name="color"
                    onChange={(e) => setcolor(e.target.value)}
                    required
                    value={ecolor}
                  />
                </div> 

              </form>
            </div>
            <div className="modal-footer">
              <button
                ref={refclose}
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
              >
                Close
              </button>
              <button
                // disabled={eitem_name.length === 0 || eitem_price.length === 0}
                type="button"
                className="btn btn-primary"
                onClick={handleclick_update}
              >
                Update
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* ----------------- UPDATE END ---------------------------- */}
 
      <button className="btn btn-success mx-2" onClick={handleclick} style={{margin:"5px"}}>
       <i className="fa fa-plus-circle"></i>  Add New Stock
      </button>


      <button className="btn btn-success mx-2" onClick={(e) => {
      e.preventDefault();
      window.open("http://google.com", "_blank");
      }} 
      style={{margin:"5px"}}>
       <i className="fa fa-plus-circle"></i>  Throwing to other pg
      </button>
      
  
      {/* { <table className="table table-striped my-3">
        <thead>
          <tr>
            <th>Delete</th>
            <th>Edit</th>
            <th>Date</th>
            <th>Item Name</th>
            <th>Item Code</th>
            <th>Quantity</th>
            <th>Size</th>
            <th>Buying Price</th>
            <th>Rental Price</th>
            <th>Color</th>
            <th>Branch</th>
          </tr>
        </thead>
        <tbody>
          {contextStock.stockdata.length === 0 ? (
            <tr>
              <td colSpan="11">No item to display</td>
            </tr>
          ) : (
            contextStock.stockdata.map((item_data, i) => (
              refs_chk.push(React.createRef()),
              <tr key={i}>
                <td>
                      <input
                          type="checkbox"
                          name="languages"
                          value={item_data._id}
                          id="flexCheckDefault"  
                          className="myCheck"
                          onChange={handleChange} ref={refs_chk[i]}
                      />
                </td>
                <td>
                  <i className="far fa-edit mx-2"></i>
                </td>
                <td>{item_data.item_name}</td>
                <td>{item_data.item_code}</td>
                <td>{item_data.quantity}</td>
                <td>{item_data.item_code}</td>
                <td>{item_data.size1}</td>
                <td>{item_data.buyprice}</td>
                <td>{item_data.rentprice}</td>
                <td>{item_data.color}</td>
                <td>{item_data.branch}</td>
              </tr>
            ))
          )}
        </tbody>
      </table> } */}

{ 
        
<ToolkitProvider
        bootstrap4
        keyField='_id'
        data={contextStock.stockdata}
        columns={columns}
        search
        wrapperClasses="table-responsive"
      >
        {
          props => (
            <>

{/* Card is started */}
<div className="card">
  <div className="card-header" style={{backgroundColor:"#80808061"}}>
          <h2 className="opacity-75">
          <u><center>          
                <i className="fa fa-empire" style={{marginRight:"10px"}}></i>
                     Stock Report
                <i className="fa fa-empire" style={{marginLeft:"10px"}}></i>
            </center></u>            
            </h2>
  </div>            
  <ul className="list-group list-group-flush">
    <li className="list-group-item" style={{backgroundColor:"#afaaab0d"}}>
{/* Card is over */}


<div className="row">
  <div className="col-sm-6">
    <button className="btn btn-danger" onClick={() => { 
             if(peopleInfo.length===0)
             {
                document.body.scrollTop = document.documentElement.scrollTop = 0;
                 ////---------- alert
                contextalert.alertmsgupdate("Please select record to delete","alert alert-warning");
                    setTimeout(() => {
                    contextalert.alertmsgupdate(null,"");
                    }, 2000);
                    //---------- end 
             }
             else
             {
              refdelopen.current.click();
             } 
                     }}
>Delete Records</button>  
</div>
  <div className="col-sm-2"> </div>
  <div className="col-sm-4"><SearchBar  className="mx-3" {...props.searchProps}/> 
<ClearSearchButton   className="mx-3" {...props.searchProps} />  
  </div>
</div> 
              <hr />
              <BootstrapTable
                defaultSorted={defaultSorted}
                pagination={pagination}
                {...props.baseProps}
                selectRow={{
                  mode: 'checkbox',
                  bgColor: 'whitesmoke',
                  headerColumnStyle: { backgroundColor: 'whitesmoke',width:'40px' },
                  onSelectAll: (isSelect, rows, e) => {  
                   
                   handleChange1(isSelect,rows)
                 },
                 onSelect: (row, isSelect, rowIndex, e) => { 
                   
                     handleChange(row._id,isSelect) 
                 }
                 //hideSelectAll: true 
                 }}
                 filter={ filterFactory() }
                 noDataIndication= "No data to display"
              />
 

{/* Card code is started */}
</li>
  </ul>
</div>
{/* Card code is started */} 

            </>
          )
        }
      </ToolkitProvider>
}

<br></br><br></br>
    </div>
    
  );
};

export default StockData;
