import { useState } from "react";
import NoteContext from "./NoteContextStock";
 

const NoteState_stock = (props) => {
  const host = "http://localhost:5000";
 
  const notesinitial = [];
  const [stockdata, setstockdata] = useState(notesinitial);

  //---------------- for API FUNCTIONALITIES --------------------------
 
  // Add a Item
  const add_stock = async (date1,item_name,item_code,quantity,size1,buyprice,rentprice,color,branch,session_city) => {
    const response = await fetch(`${host}/api/stock/addstock`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token": localStorage.getItem('token')
      },
      body: JSON.stringify({ date1,item_name,item_code,quantity,size1,buyprice,rentprice,color,branch,session_city }), // body data type must match "Content-Type" header
    });
    //const note = await response.json();
    //setitem(items.concat(note)); 
  };
  //
 
  // Delete a Item
  const delete_stock = async (ids) => {
    const response = await fetch(`${host}/api/stock/deletestock`, {
      method: "DELETE", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token')
      },
      body: JSON.stringify({ids}),
    }); 
    
    
       
     const newitems = stockdata.filter((item1) => {
     
      return !ids.includes( item1._id ); 
      //return item1._id !== id;      ///------- For a single value delete

    }); 
    setstockdata(newitems);
    //getitem(localStorage.getItem('city'));
  };
  //

    // ------------- select Item ----------------- 
    const getstock = async (session_city) => {
      //API CALL
      const response = await fetch(`${host}/api/stock/fetchstock`, {
        method: "POST", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json",
          "auth-token":localStorage.getItem('token')
        },
        body: JSON.stringify({session_city}),
      });
      const json = await response.json();
      setstockdata(json);
    };

    
  // Edit a note
  const editstock=async (id,eitem_name,eitem_code,esize1,ebuyprice,erentprice,ecolor)=>{
    //API CALL
    const response = await fetch(`${host}/api/stock/updatestock/${id}`, {
      method: "PUT", // *GET, POST, PUT, DELETE, etc. 
      headers: {
        "Content-Type": "application/json" 
      },
      body: JSON.stringify({eitem_name,eitem_code,esize1,ebuyprice,erentprice,ecolor}) // body data type must match "Content-Type" header
    });
    const json = await response.json(); // parses JSON response into native JavaScript objects
    

    let newitems=JSON.parse(JSON.stringify(stockdata))
    //logic to edit in client
    for (let index = 0; index < newitems.length; index++) {
      const element = newitems[index];
      if(element._id===id)
      {
        newitems[index].item_name=eitem_name;
        newitems[index].item_code=eitem_code; 
        newitems[index].size1=esize1; 
        newitems[index].buyprice=ebuyprice; 
        newitems[index].rentprice=erentprice; 
        newitems[index].color=ecolor;  
        break;
      }
     
    }    
    setstockdata(newitems);
   
}



    
  return (
    <NoteContext.Provider
      value={{ add_stock,delete_stock,getstock,stockdata,setstockdata,editstock }}
    >
      {props.children}
    </NoteContext.Provider>
  );
};
export default NoteState_stock;
