import React, {  useContext, useEffect,useRef, useState } from "react";
import NoteContextCreateUser from "../context/notes/NoteContextCreateUser";
import noteContextalert from "../context/notes/NoteContextalert"; 
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.css';
import BootstrapTable from 'react-bootstrap-table-next';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, {Search} from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';
import { useNavigate  } from 'react-router-dom'; 

const CreateUserSelectData = () => {

    const context = useContext(NoteContextCreateUser); 
    const contextalert = useContext(noteContextalert);

    const refs_chk = []

    const navigate = useNavigate();

  useEffect(() => {
    if(localStorage.getItem('token'))
    {
      context.getuser();
    }
    else
    {
      navigate("/login");
    }     
   
  // eslint-disable-next-line
}, []);


// --------------- for modal for hard booking 

const ref1 = useRef(null);
const refclose = useRef(null);
const refdelopen = useRef(null);
const refdelclose = useRef(null);


let flgformodalclose=true;
 
  
     //------------ DELETE
     const handleclick_delete = () => {
    
      context.deletuser(peopleInfo,"mahadev");  
       document.body.scrollTop = document.documentElement.scrollTop = 0;
       contextalert.alertmsgupdate("User has been deleted successfully","alert alert-danger");
       setTimeout(() => {
       contextalert.alertmsgupdate(null,"");
       }, 2000);
       //---------- end
        
       flgformodalclose=false;
       refdelclose.current.click();
       setPeopleInfo([]);
       
  };
  


  
   //------------ DELETE
   const handleclick_close = () => {
      //---------- alert
     
      if(flgformodalclose===true)
      {
        document.body.scrollTop = document.documentElement.scrollTop = 0;
        contextalert.alertmsgupdate("Cancelled , your data is safe","alert alert-info");
        setTimeout(() => {
         contextalert.alertmsgupdate(null,"");
        }, 2000); 
      }
     //---------- end
};

 


  //------------- for multiple checkboxes-------------  

  const [peopleInfo, setPeopleInfo] = useState([]);
  const handleChange = (chk_id,isselect) => {
   
    if(isselect)
    {
      
      setPeopleInfo([
        ...peopleInfo,
        chk_id 
      ]);
   
    }
    else
    {
      
      setPeopleInfo(peopleInfo.filter((item1) => { 
          return !chk_id.includes( item1 ); 
          //return item1._id !== id;      ///------- For a single value delete 
        }));   
    }   
  };
   

  
  const handleChange1 = (isSelect,chk_ids) => {
    // console.log(isSelect)
    // console.log(chk_ids);  
    if(isSelect){
      setPeopleInfo([]);
      var all_ids = chk_ids.map(item => item._id);
      // console.log(all_ids)
      setPeopleInfo(
        ...peopleInfo,
        all_ids 
      ); 
    }
    else
    {
      setPeopleInfo([]);
    }
    
  };

 
const columns=[ 
  {
    text: "User Name",
    dataField: "name",
    sort: true,
    editable: false,
   //style: { backgroundColor: 'green' }
   //  headerAlign: 'center',
  //  align: 'center'
   headerStyle: { backgroundColor: 'whitesmoke' } 
  
  },
  {
    text: "Password",
    dataField: "password",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke' } 
    
  },
  ,
  {
    text: "City",
    dataField: "city",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke' } 
    
  },
 
];

const defaultSorted = [{
  dataField: 'name',
  order: 'desc'
}];

//------------pagination property
const pagination = paginationFactory({
  page: 1,
  sizePerPageList: [ {
    text: '5', value: 5
  }, {
    text: '10', value: 10
  },
  {
    text: '25', value: 25
  }, {
    text: '50', value: 50
  } ],
  lastPageText: '>>',
  firstPageText: '<<',
  nextPageText: '>',
  prePageText: '<',
  showTotal: true,
  alwaysShowAllBtns: true,
  // onPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // },
  // onSizePerPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // }
});  
const { SearchBar, ClearSearchButton } = Search;


  return (
    <div>
      
       {/* ----------------- FOR DELETE MODAL ----------------------------- */}
            
<button ref={refdelopen}  type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#exampleModalCenter">
  Launch demo1
</button>
 
<div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
  <div className="modal-dialog modal-dialog-centered" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title" id="exampleModalLongTitle">Delete Record</h5>
        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div className="modal-body">
      Are you sure to delete the selected records?
      </div>
      <div className="modal-footer">
        <button type="button" ref={refdelclose} className="btn btn-secondary" data-dismiss="modal" onClick={handleclick_close}>Close</button>
        <button type="button" className="btn btn-primary" onClick={handleclick_delete}>Delete</button>
      </div>
    </div>
  </div>
</div>

      {/* ----------------- DELETE END ---------------------------- */}



          {/* ----------------- FOR UPDATE ----------------------------- */}
            
<button ref={ref1}  type="button" className="btn btn-primary d-none" data-toggle="modal" data-target="#exampleModalCenter">
  Launch demo modal
</button>
 
<div className="modal fade" id="exampleModalCenter" tabIndex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
  <div className="modal-dialog modal-dialog-centered" role="document">
    <div className="modal-content">
      <div className="modal-header">
        <h5 className="modal-title" id="exampleModalLongTitle">Delete Record</h5>
        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div className="modal-body">
      Are you sure to delete the selected records?
      </div>
      <div className="modal-footer">
        <button type="button" ref={refclose} className="btn btn-secondary" data-dismiss="modal" onClick={handleclick_close}>Close</button>
        <button type="button" className="btn btn-primary" onClick={handleclick_delete}>Delete</button>
      </div>
    </div>
  </div>
</div>

      {/* ----------------- UPDATE END ---------------------------- */}

      {/* Card is started */}
      <div className="card">
  <div className="card-header" style={{backgroundColor:"#80808061"}}>
          <h2 className="opacity-75">
          <u><center>          
                <i className="fa fa-empire" style={{marginRight:"10px"}}></i>
                      User Data
                <i className="fa fa-empire" style={{marginLeft:"10px"}}></i>
            </center></u>            
            </h2>
  </div>            
  <ul className="list-group list-group-flush">
    <li className="list-group-item" style={{backgroundColor:"#afaaab0d"}}>
{/* Card is over */}



{/* 
              <table className="table table-striped">
          <thead>
            <tr>
              <th>Delete</th> 
              <th>User Name</th>
              <th>Password</th>
              <th>City</th>
            </tr>
          </thead>
          <tbody>
            
          { 
          context.users.length === 0 ? (
              <tr>
                <td colSpan="4">No item to display</td>
              </tr>
            ) : (
             
              context.users.map((item_data, i) => (
                refs_chk.push(React.createRef()),
                <tr key={i}>
                  <td>
                  <input
					type="checkbox"
					name="languages"
					value={item_data._id}
					id="flexCheckDefault"  
          className="myCheck"
          onChange={handleChange} ref={refs_chk[i]}
				/>
        
                  </td> 
                  <td>{item_data.name}</td>
                  <td>{item_data.password}</td>
                  <td>{item_data.city}</td>
                </tr>
              ))
            )}
          </tbody>
        </table> */}


<ToolkitProvider
        bootstrap4
        keyField='_id'
        data={context.users}
        columns={columns}
        search 
        wrapperClasses="table-responsive"
        
      >
        {
          props => (
            <> 
  <div className="row">
  <div className="col-sm-6"> 
  <button className="btn btn-danger" onClick={() => {  
   
   // console.log(peopleInfo)
    
    if(peopleInfo.length===0)
    {
       document.body.scrollTop = document.documentElement.scrollTop = 0;
        ////---------- alert
       contextalert.alertmsgupdate("Please select record to delete","alert alert-warning");
           setTimeout(() => {
           contextalert.alertmsgupdate(null,"");
           }, 2000);
           //---------- end 
    }
    else
    {
     refdelopen.current.click();
    }  
    }} >Delete Records</button> 

  </div>
  <div className="col-sm-2"> </div>
  <div className="col-sm-4">
    <SearchBar  className="mx-3" {...props.searchProps}/> 
    <ClearSearchButton  className="mx-3" {...props.searchProps} />  
  </div>
</div> 
              <hr></hr>
              <BootstrapTable  
                defaultSorted={defaultSorted}
                pagination={pagination}
                {...props.baseProps}
                selectRow={{
                   mode: 'checkbox',
                   bgColor: 'whitesmoke',
                   headerColumnStyle: { backgroundColor: 'whitesmoke',width:'40px' },
                   onSelectAll: (isSelect, rows, e) => {   
                    handleChange1(isSelect,rows)
                  },
                  onSelect: (row, isSelect, rowIndex, e) => { 
                    handleChange(row._id,isSelect) 
                  }
                  //hideSelectAll: true

                  }}
                 
                  noDataIndication= "No data to display"
                  
              />
            </>
          )
        }
      </ToolkitProvider>
     

  {/* Card code is started */}
  </li>
    </ul>
  </div>
  {/* Card code is started */}

    </div>
  )
}

export default CreateUserSelectData
