import React, { useState, useContext,useEffect } from "react";
import NoteContextaddcusdetail from "../context/notes/NoteContextaddcusdetail";
import noteContextalert from "../context/notes/NoteContextalert";
import { useLocation  } from 'react-router-dom'; 
import { useNavigate  } from 'react-router-dom'; 

const Addcustomerdetail = () => {
  
  //--------------- FOR CONTEXT ------------------------
  const contextaddcusdetail = useContext(NoteContextaddcusdetail);
// ---------------------  END --------------------------------- 

 

//------------ usestate variables ----------------------
const [mob, setmob] = useState("");
const [name, setname] = useState("");
const [address, setaddress] = useState("");
const [advance, setadvance] = useState("");
const [deposite, setdeposite] = useState("");
const [itemprice, setitemprice] = useState("");
//------------------- end --------------------------


const location = useLocation();
const navigate = useNavigate();

useEffect(() => {
  contextaddcusdetail.getcustmob(localStorage.getItem("city")); 
  contextaddcusdetail.gettotalprice(localStorage.getItem("city"),location.state.billno);   
  contextaddcusdetail.setcustdata([]);
  // eslint-disable-next-line
}, []);


 
  //------------ GET customer data
  const getcustdata = async (e) => {  
   
    let mobileno = document.getElementById("txt_mob_no").value;   
    // const response = await fetch(
    //   `http://localhost:5000/api/add_customer_detail/fetchcustomerdata`,
    //   {
    //     method: "POST", // *GET, POST, PUT, DELETE, etc.
    //     headers: {
    //       "Content-Type": "application/json",
    //       "auth-token": localStorage.getItem("token"),
    //     },
    //     body: JSON.stringify({ mobileno }),
    //   }
    // ); 
    // const json = await response.json();
    // setname(json.name) 
    // setaddress(json.address) 
    
    

    contextaddcusdetail.getcustdata(localStorage.getItem("city"),mobileno); 
  };

    //------------ HARDLY CLICK KARE TYARE MODAL MA
    const handleclick_update_bill_table = (e) => {  
      
  
   
      // ------------------ INSERT INTO BILL 
    let finalfigure = document.getElementById("itempricetotal").value;
    let cut_nm = document.getElementById("cut_nm").value;
    let cut_address = document.getElementById("txt_address").value; 
    let txt_mob_no = document.getElementById("txt_mob_no").value; 
   
    let remaining=""
    { 
      contextaddcusdetail.sumitemprice.map((item_data, i) => (
        remaining = item_data.sum_val - advance
     ))} 

    

    contextaddcusdetail.updatebilltable(cut_nm,txt_mob_no,cut_address,finalfigure,advance,remaining,deposite,localStorage.getItem("city"),location.state.billno); 
    navigate('/generate_bill'); 
    };

  
  return (
  
    <div className='container'>
 
    {/* Card is started */}
    <div className="card">
  <div className="card-header" style={{backgroundColor:"#80808061"}}>
          <h2 className="opacity-75"><u><center>
                <i className="fa fa-empire" style={{marginRight:"10px"}}></i>
                Add Customer Details
                <i className="fa fa-empire" style={{marginLeft:"10px"}}></i>    
          </center></u></h2>
  </div>            
  <ul className="list-group list-group-flush">
    <li className="list-group-item" style={{backgroundColor:"#afaaab0d"}}>
{/* Card is over */}


<table className="table table-striped table-hover">
    <tbody>
     
      <tr>
        <td><label className="form-label">Mobile No</label></td>
        <td>
          <input
            list='browsers'
            name='txt_mob_no'
            id='txt_mob_no'
            className='form-control'
            onChange={getcustdata} 
            required
          ></input>
           <datalist id='browsers'>
            {/* {contextaddcusdetail.mobileno.map((item_data, i) => (
              <option key={i} value={item_data.Mobile_No}>
                {item_data.Mobile_No}
              </option>
            ))} */}
              {contextaddcusdetail.mobileno.map((item_data, i) => ( 
                  <option key={i} value={item_data.Mobile_No}>{item_data.Mobile_No}</option>  
            ))}   
          </datalist>
        </td>
      </tr> 
      <tr>
        <td><label className="form-label">Customer Name</label></td>
{/* <td>
            <input
            type="text"
            className="form-control"
            id="cut_nm"
            name="cut_nm"  
            defaultValue={
              contextaddcusdetail.custdata.map((item_data, i) => (
                item_data.Name
             ))
            }
            onChange={(e) => setname(e.target.value)}
          />
</td> */}

<td><textarea id="cut_nm" name="cut_nm" className="form-control" defaultValue={      
               contextaddcusdetail.custdata.map((item_data, i) => (
                 item_data.Name
              ))
             } onChange={(e) => setname(e.target.value)}>
              </textarea></td>
      </tr>

      <tr>
        <td><label className="form-label">Address</label></td>
<td><textarea id="txt_address" name="txt_address" className="form-control" defaultValue={
               
              contextaddcusdetail.custdata.map((item_data, i) => (
                item_data.Address
             ))
            } onChange={(e) => setaddress(e.target.value)}>
             </textarea></td>
      </tr>
 

      <tr>
      <td><label className="form-label">Total Rs *</label></td>
        <td> 
        
            { 
              contextaddcusdetail.sumitemprice.map((item_data, i) => (
                <input key={i}
                type="text"
                className="form-control"
                id="itempricetotal"
                name="itempricetotal" 
                value={  
                  item_data.sum_val
                }
                onChange={(e) => setitemprice(e.target.value)}
              />  
             ))}
			 
		 
			
       
</td>
      </tr>


      <tr>
      <td><label className="form-label">Advance *</label></td>
        <td> 
        <input
            type="number"
            className="form-control"
            id="txt_advance"
            name="txt_advance"
            onChange={(e) => setadvance(e.target.value)}  
          />
</td>
      </tr>
     
      <tr>
      <td><label className="form-label">Deposite *</label></td>
        <td> 
        <input
            type="number"
            className="form-control"
            id="txt_deposite"
            name="txt_deposite"
            onChange={(e) => setdeposite(e.target.value)}  
          />
</td>
      </tr>

      <tr>
      <td><label className="form-label">Remaining Rs *</label></td>
        <td> 
          
        { 
              contextaddcusdetail.sumitemprice.map((item_data, i) => (
                item_data.sum_val - advance  
             ))} 
      </td>   
      </tr>

      <tr>
      <td colSpan={2} style={{textAlign:"right"}}>
      <button onClick={handleclick_update_bill_table}   className="btn btn-info" style={{width:"80%"}}>
        Add
      </button>
      </td>
      </tr>
     

    </tbody>
    </table>
    
    
{/* Card code is started */}
</li>
  </ul>
</div>
{/* Card code is started */}
  </div>
  
  )
}

export default Addcustomerdetail
