import React, { useState,useContext } from "react";
import { useNavigate  } from 'react-router-dom'; 
import noteContextalert from "../context/notes/NoteContextalert";

const Login = () => {

  const [name, setname] = useState("");
  const [password, setpassword] = useState("");
  
  const navigate = useNavigate();
   
  const contextalert = useContext(noteContextalert);


  const handleclick = async (e) => {
    e.preventDefault();
    const response = await fetch("http://localhost:5000/api/auth/login", {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ name, password }),
    });
    const json = await response.json(); // parses JSON response into native JavaScript objects
    if (json.success) {
      localStorage.setItem('token', json.authtoken); // cookie
      localStorage.setItem('name', json.name); // cookie
      localStorage.setItem('city', json.city); // cookie

      // alert(localStorage.getItem('token'));
      // alert(localStorage.getItem('city'));
      navigate("/"); // header:location
     
    } else {
         // //---------- alert
         contextalert.alertmsgupdate(json.errors,"alert alert-danger");
         setTimeout(() => {
           contextalert.alertmsgupdate(null,"");
         }, 2000);
         // //---------- end   
    }
  };


  return (

    // <div className='container'>
    // <h2><u>Login Here</u></h2>
    // <form onSubmit={handleclick}> 
    //   <div className="mb-3">
    //     <label htmlhtmlFor="exampleInputEmail1" className="form-label">
    //       Name
    //     </label>
    //     <input
    //       type="text"
    //       className="form-control"
    //       id="name"
    //       name="name"
    //       aria-describedby="emailHelp"
    //       onChange={(e) => setname(e.target.value)} required
    //     />
    //   </div>
    //   <div className="mb-3">
    //     <label htmlhtmlFor="exampleInputPassword1" className="form-label">
    //       Password
    //     </label>
    //     <input
    //       type="password"
    //       className="form-control"
    //       id="password"
    //       name="password"
    //       onChange={(e) => setpassword(e.target.value)} autoComplete="on" required
    //     />
    //   </div>
    //   <button type="submit" className="btn btn-primary">
    //     Login
    //   </button>
    // </form>
    // </div>
    
  <div className="container-fluid h-custom">
    <div className="row d-flex justify-content-center align-items-center h-100">
      <div className="col-md-9 col-lg-6 col-xl-5">
        <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/draw2.webp"
          className="img-fluid" alt="Sample image"/>
      </div>
      <div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
        <form onSubmit={handleclick}>
          
          <div className="divider d-flex align-items-center my-4">
            <p className="text-center fw-bold mx-3 mb-0" style={{"fontSize":"30px"}}>Log in here</p>
          </div>

           
          <div className="form-outline mb-4">
            <input type="text" id="form3Example3" className="form-control form-control-lg"
              placeholder="Enter User Name"
              onChange={(e) => setname(e.target.value)} required /> 
          </div>

       
          <div className="form-outline mb-3">
            <input type="password" id="form3Example4" className="form-control form-control-lg"
              placeholder="Enter Password"
              onChange={(e) => setpassword(e.target.value)} autoComplete="on" required />
            
          </div>

         

          <div className="text-center text-lg-start mt-4 pt-2">
            <button type="submit" className="btn btn-success btn-lg"
              style={{"paddingLeft":" 2.5rem","paddingRight": "2.5rem"}}>Login</button>
           
          </div>

        </form>
      </div>
    </div>
  </div>
  
  )
}

export default Login
