import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.css';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
 import ToolkitProvider, {Search} from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';
 import {CSVExport} from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';

const BootstrapPagination = () => {
    const products = [
        { id: 1, name: 'George', animal: 'Monkey12121221212121212112121212121878787878788',roll:'5' },
        { id: 2, name: 'Jeffrey', animal: 'Giraffe',roll:'6' },
        { id: 3, name: 'Alice', animal: 'Giraffe',roll:'5' },
        { id: 4, name: 'Foster', animal: 'Tiger',roll:'5' },
        { id: 5, name: 'Tracy', animal: 'Bear',roll:'5' },
        { id: 6, name: 'Joesph', animal: 'Lion',roll:'5' },
        { id: 7, name: 'Tania', animal: 'Deer' ,roll:'5'},
        { id: 8, name: 'Chelsea', animal: 'Tiger',roll:'5' },
        { id: 9, name: 'Benedict', animal: 'Tiger',roll:'5' },
        { id: 10, name: 'Chadd', animal: 'Lion' ,roll:'5'},
        { id: 11, name: 'Delphine', animal: 'Deer',roll:'5' },
        { id: 12, name: 'Elinore', animal: 'Bear',roll:'5' },
        { id: 13, name: 'Stokes', animal: 'Tiger',roll:'5' },
        { id: 14, name: 'Tamara', animal: 'Lion',roll:'5' },
        { id: 15, name: 'Zackery', animal: 'ion',roll:'5' }
      ];
    
      const columns = [
        { dataField: 'id', text: 'Id', sort: true, width: 15 },
        { dataField: 'name', text: 'Name', sort: true, minimal: 'lg' },
        { dataField: 'animal', text: 'Animal', sort: true, minimal: 'lg' },
        { dataField: 'roll', text: 'roll', sort: true, minimal: 'lg' }
        
        
      ];
    
      const defaultSorted = [{
        dataField: 'name',
        order: 'desc'
      }];
    
      const pagination = paginationFactory({
        page: 2,
        sizePerPage: 5,
        lastPageText: '>>',
        firstPageText: '<<',
        nextPageText: '>',
        prePageText: '<',
        showTotal: true,
        alwaysShowAllBtns: true,
        onPageChange: function (page, sizePerPage) {
          console.log('page', page);
          console.log('sizePerPage', sizePerPage);
        },
        onSizePerPageChange: function (page, sizePerPage) {
          console.log('page', page);
          console.log('sizePerPage', sizePerPage);
        }
      });
    
      const { SearchBar, ClearSearchButton } = Search;
      const { ExportCSVButton } = CSVExport;

      const MyExportCSV = (props) => {
        const handleClick = () => {
          props.onExport();
        };
        
        return (
            

          <div>
            <button className="btn btn-success" onClick={handleClick}>Export to CSV</button>
          </div>
        );
      };


  return (
    <div>
        <h5>React Bootstrap Table Next with Sorting, Pagination and Search</h5>

        <ToolkitProvider
        bootstrap4
        keyField='id'
        data={products}
        columns={columns}
        search
        wrapperClasses="table-responsive"
      >
        {
          props => (
            <div>
              <h6>Input something at below input field:</h6>
              <SearchBar {...props.searchProps} />
              <ClearSearchButton {...props.searchProps} />
              
              <BootstrapTable
                defaultSorted={defaultSorted}
                pagination={pagination}
                {...props.baseProps}
              />
            </div>
          )
        }
      </ToolkitProvider>
    </div>
  )
}

export default BootstrapPagination