import React from "react";
import {
    Link,useNavigate
  } from "react-router-dom";
 

const Navbar = () => {
  
  const navigate = useNavigate();

  const handleclicklogout=()=>{
    localStorage.removeItem('token'); 
    localStorage.removeItem('city'); 
    navigate("/login");
  }

  return (
    <>
    <nav className="navbar navbar-expand-lg bg-dark fixed-top" style={{marginBottom:"100px",color:"white"}}>
    <div className="container-fluid">
      <Link className="navbar-brand" to="/" style={{color:"#93C572",fontSize:"20px",fontFamily:"Algerian"}}>
        Rental Booking App  
      </Link>
      <button
        className="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
        style={{color:"#93C572"}}
      >
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" style={{color:"white"}} to="/">
              Stock
            </Link>   
          </li>

          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" style={{color:"white"}} to="/generate_bill">
              Generate Bill
            </Link>   
          </li>

          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" style={{color:"white"}} to="/deposite">
              Deposite
            </Link>   
          </li>


          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" style={{color:"white"}} to="/item_master">
              Item Master
            </Link>   
          </li>

          <li className="nav-item">
            <Link className="nav-link active" aria-current="page" style={{color:"white"}} to="/user_data">
              User Data
            </Link>   
          </li>
 
        
         
        </ul>
        
      
         {
         !localStorage.getItem('token')?
         <form className="d-flex">
         <Link className="btn btn-secondary mx-2" to="/login" role="button">
           Login
         </Link> 
         <Link className="btn btn-success mx-2" to="/create_user" role="button">
           Sign Up
         </Link> 
         </form>
          :
          <>

      <div className="navbar-nav ml-auto">  
          <a className="nav-item nav-link btn btn-sm btn-light" style={{color:"blank",fontsize:"18px"}}><i style={{fontsize:'20px'}} className='fa fa-user' aria-hidden='true'></i>&nbsp;&nbsp;{localStorage.getItem('name')}</a>
      </div>

         <button className="btn btn-secondary mx-2" onClick={handleclicklogout}  style={{color:"white"}}>
        Logout
      </button>
 
      

      
        </>
        }
       
          
        
      </div>
    </div>
  </nav>
<br></br><br></br><br></br><br></br>
</>
  )
}

export default Navbar
