import { useState } from "react";
import NoteContextdeposite from "./NoteContextdeposite";
 

const NoteStatedeposite = (props) => {
  const host = "http://localhost:5000";
  
  const notesinitial = []; 
  const [firsttray, setfirsttray] = useState(notesinitial); 
  const [secondtray, setsecondtray] = useState(notesinitial); 
  const [thirdtray, setthirdtray] = useState(notesinitial); 
 
 

  //---------------- for API FUNCTIONALITIES --------------------------

  // ------------- select Item ----------------- 
  const getfirsttray = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/deposite/fetchfirsttray`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      },
      body: JSON.stringify({session_city}),
    });
    const json = await response.json();
    setfirsttray(json);
  }; 
 
    // ------------- select Item ----------------- 
  const getsecondtray = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/deposite/fetchsecondtray`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      },
      body: JSON.stringify({session_city}),
    });
    const json = await response.json();
    setsecondtray(json);
  }; 

   // ------------- select Item ----------------- 
   const getthirdtray = async (session_city) => {
    //API CALL
    const response = await fetch(`${host}/api/deposite/fetchthirdtray`, {
      method: "POST", // *GET, POST, PUT, DELETE, etc.
      headers: {
        "Content-Type": "application/json",
        "auth-token":localStorage.getItem('token'), 
      },
      body: JSON.stringify({session_city}),
    });
    const json = await response.json();
    setthirdtray(json);
  }; 


    // ------------- select Item ----------------- 
    const udepositefirstttray = async (session_city,deposite,bill_no) => {
      //API CALL
      const response = await fetch(`${host}/api/deposite/udepositefirsttray`, {
        method: "PUT", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json",
          "auth-token":localStorage.getItem('token'), 
        },
        body: JSON.stringify({session_city,deposite,bill_no}),
      });
      const json = await response.json();
          

     const newitems = firsttray.filter((firsttray1) => {
      return ![bill_no].includes( firsttray1.bill_no );  
    });  
    setfirsttray(newitems);

   

    const newitems1 = firsttray.filter((firsttray11) => {
      return [bill_no].includes( firsttray11.bill_no );  
    });  

    let newitems2=JSON.parse(JSON.stringify(newitems1))
    //logic to edit in client
    for (let index = 0; index < newitems2.length; index++) {
      const element = newitems2[index];
      if(element.bill_no===bill_no)
      {
        newitems2[index].Deposite=deposite; 
        //break;
      }
     
    }    
    setsecondtray(secondtray.concat(newitems2)); 
    

    }; 


    // ------------- select Item ----------------- 
    const udepositesecondtray = async (session_city,bill_no) => {
      //API CALL
      const response = await fetch(`${host}/api/deposite/udepositesecondtray`, {
        method: "PUT", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json",
          "auth-token":localStorage.getItem('token'), 
        },
        body: JSON.stringify({session_city,bill_no}),
      });
      const json = await response.json();  
      const newitems = secondtray.filter((secondtray1) => {
        return ![bill_no].includes( secondtray1.bill_no );  
      });  
      setsecondtray(newitems);
      
      
      const newitems1 = secondtray.filter((secondtray1) => {
        return [bill_no].includes( secondtray1.bill_no );  
      });  

      setthirdtray(thirdtray.concat(newitems1));
    }; 


 
  return (
    <NoteContextdeposite.Provider
      value={{getfirsttray,firsttray,setfirsttray,getsecondtray,secondtray,setsecondtray,udepositefirstttray,udepositesecondtray,getthirdtray,thirdtray,setthirdtray}}
    >
      {props.children}
    </NoteContextdeposite.Provider>
  );
};
export default NoteStatedeposite;
