import axios from "axios";
import React, {  useContext, useEffect, useState,useRef } from "react";
import noteContext from "../context/notes/NoteContextItemMaster"; 
import noteContextalert from "../context/notes/NoteContextalert";
import ReactPaginate from "react-paginate";
import './Imagecss.css';
import { useNavigate  } from 'react-router-dom'; 


const Item_master = () => {
  const navigate = useNavigate();
  const moredetail = (e) => {
    navigate("/generate_bill"); // header:location
  };

  
  const [imagecontetntt, setimagecontetntt] = useState([]);
  
  const context = useContext(noteContext); 
  const contextalert = useContext(noteContextalert);
  
  //--------------------- FOR Pagination ---------------------------------
  const [currentItems,setcurrentItems]=useState([]);
  const [pageCount,setpagecount]=useState(0);
  const [itemOffset,setItemOffset]=useState(0);
  const itemsPerPage=6;

  useEffect(() => { 
    const fetchData = async () => {
      const response = await fetch("http://localhost:5000/api/item_master/fetchimage", {
        method: "POST", // *GET, POST, PUT, DELETE, etc.
        headers: {
          "Content-Type": "application/json",
        }
      });
      const json = await response.json(); // parses JSON response into native JavaScript objects    
      setimagecontetntt(json);
    }
    // call the function
    fetchData()
    // make sure to catch any error
    .catch(console.error);
// eslint-disable-next-line
}, []);


  useEffect(()=>{
    const endOffset = itemOffset + itemsPerPage;
    setcurrentItems(imagecontetntt.slice(itemOffset,endOffset));
    setpagecount(Math.ceil(imagecontetntt.length/itemsPerPage));
  },[itemOffset,itemsPerPage,imagecontetntt]);
  

   // Invoke when user click to request another page.
   const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % imagecontetntt.length; 
    setItemOffset(newOffset);
  };
//-------------------------- End Pagination ---------------------------------------

  // Invoke when user click to request another page.
  const deleteimage = (id) => {
    
    console.log(id)
    // const response = await fetch("http://localhost:5000/api/item_master/deleteimage", {
    //     method: "DELETE", // *GET, POST, PUT, DELETE, etc.
    //     headers: {
    //       "Content-Type": "application/json",
    //     },
    //     body: JSON.stringify({ name, password }),
    //   });
    //   const json = await response.json(); // parses JSON response into native JavaScript objects    
    //   setimagecontetntt(json);
  };


//------------------------------ Image upload -----------------------------------------

    //-------------------- useref -------------------------------------
    const imageRefclear = useRef(null);
  
    //-------------------------- usestate ---------------------------------
    const [imgCollection, setimgCollection] = useState('');
    const [name, setname] = useState('');
    //---------------------------------end---------------------


    const onFileChange = (event) => {
        setimgCollection(event.target.files); 
    }

   
    

    const onSubmit = async (e) => {
        e.preventDefault() 
        
        if(imgCollection.length===0)
        {
            contextalert.alertmsgupdate("Please select image to upload","alert alert-warning");
            setTimeout(() => {
              contextalert.alertmsgupdate(null,"");
            }, 2000);
            
        }
        else
        {
          
            var formData = new FormData();  
            for (const key of Object.keys(imgCollection)) { 
                formData.append('imgCollection',imgCollection[key])
            }
            formData.append('name', name)
    
            setname("");
            imageRefclear.current.value = null;
    
    
    
            axios.post("http://localhost:5000/api/auth/upload-images", formData, {
            }).then(res => {
              setimagecontetntt(imagecontetntt.concat(res.data));
              // console.log(res.data)
            })

            
               
        }
       
    }



  return (
    // <> 
    //  <h2>MAHADEV</h2>
    // <form>
    //     <div className="form-group" >

    //         <label htmlFor="file">Upload File:</label>
    //         <input 
    //         className="form-control-file mb-3" 
    //         type="file" id="file"  
    //         onChange={handleFileChange}
    //         />

    //         <button 
    //         className="btn btn-primary mt-3" 
    //         onClick={handleSubmit}
    //         >Upload</button>
    //     </div>
    //    </form>
    //    <input type='text' onChange={(e) => setname(e.target.value)}></input>
    //    <input type='text' onChange={(e) => setaddress(e.target.value)}></input>
    <>
     {/* <FilesUploadComponent></FilesUploadComponent> */}
     <div className="container">
       
       <div className="row">
       <form>
           <div className="card text-center">
<div className="card-header">
<h5 className="card-title">Please upload single/multiple images</h5>
</div>
<div className="card-body">

<div className="container">

<div className="row">
<div className="col">
<b>Image Description ::</b>
<input type="text" name="name" value={name} onChange={(e) => setname(e.target.value)} />
</div>
<div className="col">
<b> Choose images ::</b>
<input type="file" name="imgCollection" ref={imageRefclear} onChange={onFileChange} multiple />
</div>
<div className="col">
<br></br>
<button className="btn btn-secondary" type="submit" onClick={onSubmit}>Upload</button>
</div>
</div>
</div>

</div> 
</div>
</form>
       </div>
   </div>

        <div className="images"> 
      {
        
        currentItems.map((image,i)=>(
          <div className="image" key={i}> 
            <img src={image.imgCollection[0]} alt="mahadev"></img>
            <h4 style={{color:"red"}}>{image.name}</h4>  
            <button className='delete-btn btn btn-danger btn-sm'         onClick={() => {
          deleteimage(
            image._id
          ); 
        }}><span className='fa fa-trash' /></button>
            </div>
        ))
       
      }
        </div>
    
      <ReactPaginate
        breakLabel="..."
        nextLabel="next >"
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        previousLabel="< previous"
        renderOnZeroPageCount={null}
        containerClassName="pagination"
        pageLinkClassName="page-num"
        previousLinkClassName="page-num"
        nextLinkClassName="page-num"
        activeLinkClassName="active"
      />
 
    
   
 
   
   
    </>
  )
}

export default Item_master
