import React, { useState,useContext,useEffect } from "react";
import NoteContextItemMaster from "../context/notes/NoteContextItemMaster";
import NoteContextStock from "../context/notes/NoteContextStock";
import noteContextalert from "../context/notes/NoteContextalert";
import { useNavigate  } from 'react-router-dom'; 

const Stock = () => {  
  let date_d = new Date().getDate();
  let date_m = ("0" + (new Date().getMonth()+1)).slice(-2);
  let date_y = new Date().getFullYear();
  
  const [date1, setitemdate1] = useState(date_y+"-"+date_m+"-"+date_d);
  const [item_name, setitem_name] = useState("");
  const [item_code, setitem_code] = useState("");
  const [quantity, setquantity] = useState("");
  const [size1, setsize1] = useState("");
  const [buyprice, setbuyprice] = useState("");
  const [rentprice, setrentprice] = useState("");
  const [color, setcolor] = useState(""); 
   
//--------- FOR SELECT ITEMS---------------
const contextItemMaster = useContext(NoteContextItemMaster); 
const contextStock = useContext(NoteContextStock);
const contextalert = useContext(noteContextalert);

const navigate = useNavigate();

  useEffect(() => {
    if(localStorage.getItem('token'))
    {
      contextItemMaster.getitem(localStorage.getItem("city"));
    }
    else
    {
      navigate("/login");
    } 
    // eslint-disable-next-line
  }, []);


  const changeDateFormatTo = date => {
    const [yy, mm, dd] = date.split(/-/g);
    return `${dd}/${mm}/${yy}`;
  };

  const handleclick=(e)=>{
    e.preventDefault();  
    let branch = document.getElementById("branchs").value;
    const formattedDate = changeDateFormatTo(date1);   
     contextStock.add_stock(formattedDate,item_name,item_code,quantity,size1,buyprice,rentprice,color,branch,localStorage.getItem('city'));
     setitem_name("");
     setitem_code("");
     setquantity("");
     setsize1("");
     setbuyprice("");
     setrentprice("");
     setcolor("");
    

     //---------- alert.
     document.body.scrollTop = document.documentElement.scrollTop = 0;
     contextalert.alertmsgupdate("Stock has been added successfully","alert alert-success");
     setTimeout(() => {
       contextalert.alertmsgupdate(null,"");
     }, 2000);
     //---------- end
     document.getElementById("issue_date").focus(); 
}

const movetostockdata=(e)=>{  
  navigate("/"); // header:location
  }


  return (
    <>  
     
    <div className='container'>
   
    <button className="btn btn-outline-secondary" onClick={movetostockdata} style={{margin:"5px"}}>
    <i className="fa fa-eye"></i>  View Stock Data</button>

   {/* Card is started */}
   <div className="card">
  <div className="card-header" style={{backgroundColor:"#80808061"}}>
          <h2 className="opacity-75"><u><center>
                <i className="fa fa-empire" style={{marginRight:"10px"}}></i>
                        Add Stock
                <i className="fa fa-empire" style={{marginLeft:"10px"}}></i>    
          </center></u></h2>
  </div>            
  <ul className="list-group list-group-flush">
    <li className="list-group-item" style={{backgroundColor:"#afaaab0d"}}>
{/* Card is over */}

   
    <form onSubmit={handleclick}>  

    <table className="table table-striped table-hover">
    <tbody>
      <tr>
        <td style={{textAlign:"right"}}>  Date</td>
        <td>
            <input type="date" id="issue_date" name="issue_date1" onChange={(e) => setitemdate1(e.target.value)} className="form-control" autoFocus defaultValue={date1}/>
        </td>
      </tr>
 
     <tr>
        <td style={{textAlign:"right"}}>Item Name</td> 
        <td>
              <input list='browsers' name='item' id='item' className='form-control' value={item_name} onChange={(e) => setitem_name(e.target.value)} required></input>
              <datalist id='browsers'>

            {contextItemMaster.items.map((item_data, i) => ( 
                  <option key={i} value={item_data.item_name}>{item_data.item_name}</option>  
            ))} 

              </datalist>
        </td>
    </tr>  

      <tr>
        <td style={{textAlign:"right"}}>Item Code</td>
        <td>
              <input
                type="text"
                className="form-control"
                id="item_code"
                name="item_code"
                onChange={(e) => setitem_code(e.target.value)} required value={item_code}
              />
        </td>
      </tr> 

      <tr>
        <td style={{textAlign:"right"}}>Quantity</td>
        <td>
            <input
              type="number"
              className="form-control"
              id="quantity"
              name="quantity"
              onChange={(e) => setquantity(e.target.value)}
              value={quantity}
            />
        </td>
      </tr> 

      <tr>
        <td style={{textAlign:"right"}}>Size</td>
        <td>
            <input
              type="text"
              className="form-control"
              id="size1"
              name="size1"
              onChange={(e) => setsize1(e.target.value)} required value={size1}
            />
        </td>
      </tr> 

      <tr>
        <td style={{textAlign:"right"}}>Buying Price</td>
        <td>
            <input
              type="number"
              className="form-control"
              id="buyprice"
              name="buyprice"
              onChange={(e) => setbuyprice(e.target.value)} required value={buyprice}
            />
        </td>
      </tr> 

      <tr>
        <td style={{textAlign:"right"}}>Rental Price</td>
        <td>
            <input
              type="number"
              className="form-control"
              id="rentprice"
              name="rentprice"
              onChange={(e) => setrentprice(e.target.value)} required value={rentprice}
            />
        </td>
      </tr> 

      <tr>
        <td style={{textAlign:"right"}}>Color</td>
        <td>
            <input
              type="text"
              className="form-control"
              id="color"
              name="color"
              onChange={(e) => setcolor(e.target.value)} required value={color}
            />
        </td>
      </tr> 

      <tr hidden>
          <td style={{textAlign:"right"}}>Branch</td>
          <td>
              <select className="form-control" id="branchs">
                <option value="GUJARAT">GUJARAT</option>
                <option value="MAHARASTRA">MAHARASTRA</option>
                <option value="PUNJAB">PUNJAB</option>
              </select>
          </td>
      </tr> 

   <tr>
    <td colSpan={2} style={{textAlign:"right"}}>
    <button type="submit" className="btn btn-info" style={{width:"80%"}}>
        Add
      </button>
    </td>
   </tr>
      
   </tbody> 
      </table>
    </form>

{/* Card code is started */}
</li>
  </ul>
</div>
{/* Card code is started */}


    </div>
   
    </>
  )
}

export default Stock
