import { useState } from "react";
import NoteContextalert from "./NoteContextalert";
 

const NoteStatealert = (props) => {
   
  //-------------  start code for ABOUT PAGE
  const s1 = {
    name: null,
  };
  const [state, setstate] = useState(s1);
  const alertmsgupdate = (id,type) => {
    setstate({
      name: id,
      type: type,
    });
  }; 
  //------------- end  

  
  return (
    <NoteContextalert.Provider
      value={{ state,alertmsgupdate }}
    >
      {props.children}
    </NoteContextalert.Provider>
  );
};
export default NoteStatealert;
