import React, {  useContext, useEffect,useRef,useState } from "react";
import NoteContextdeposite from "../context/notes/NoteContextdeposite"; 
import noteContextalert from "../context/notes/NoteContextalert";
import { useNavigate  } from 'react-router-dom'; 
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.css';
import BootstrapTable from 'react-bootstrap-table-next';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, {Search} from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';

const DepositeFirstTray = () => {
    const contextdeposite = useContext(NoteContextdeposite);
    const contextalert = useContext(noteContextalert);


    //----------- use state ----------------------
    const [deposite, setdeposite] = useState("");
    const [bill_no, setbillno] = useState("");

    const [final_figure,setfinalfigure] = useState("");
    const [advance,setadvance] = useState("");
    const [remaining, setremaining] = useState("");
    
    const navigate = useNavigate();

    useEffect(() => {
    if(localStorage.getItem('token'))
    {
      contextdeposite.getfirsttray(localStorage.getItem("city"));
    }
    else
    {
      navigate("/login");
    }  
    // eslint-disable-next-line
      }, []);


    //------------ use ref
    const ref1 = useRef(null);
    const refclose = useRef(null);
    //----------- end -------------

    const toggleopenfirsttray = (deposite,bill_no,Final_Figure,Advance,Remaining) => {
    ref1.current.click();
    setdeposite(deposite);
    setbillno(bill_no);
    setfinalfigure(Final_Figure);
    setadvance(Advance);
    setremaining(Remaining);
    
    };

    const handleclickfirsttray = (e) => {
        e.preventDefault();
        contextdeposite.udepositefirstttray(localStorage.getItem("city"),deposite,bill_no)
        refclose.current.click();
        //---------- alert
        document.body.scrollTop = document.documentElement.scrollTop = 0;
        contextalert.alertmsgupdate("Successfully Delivered Bill","alert alert-success");
        setTimeout(() => {
          contextalert.alertmsgupdate(null,"");
        }, 2000);
        //---------- end
      };

      let mystylefirsttray={
        fontSize:'25px',
        fontWeight:'bold',
        color:'white',
        backgroundColor:'red',
        width:'300px'
      }


       //----------------- FOR BOOTSTRAP TABLE , PAGINATION---------------------
      
      
 
const edit = (cell, row) => {
  return (
    <div>
      <i className='btn btn-danger btn-sm' onClick={() => {
          toggleopenfirsttray(
            row.Deposite,
            row.bill_no,
            row.Final_Figure,
            row.Advance,
            row.Remaining
          ); 
      }}>D</i>
    </div>
) 
 }

const columns=[ 
  {
    text: 'Delivery',
    dataField: 'Edited',
    formatter: edit,
    editable: true,
    headerStyle: { backgroundColor: 'whitesmoke',width:'80px' } 
  },
  {
    text: "Bill No",
    dataField: "bill_no",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Name",
    dataField: "Name",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Mobile No",
    dataField: "Mobile",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Delivery Date",
    dataField: "DC_flag",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Return Date",
    dataField: "repairing_flg",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Item Name",
    dataField: "Item",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Item Code",
    dataField: "Item_Code",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Total Rs",
    dataField: "Final_Figure",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Advance Rs",
    dataField: "Advance",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Remaining Rs",
    dataField: "Remaining",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "Deposite Rs",
    dataField: "Deposite",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  },
  {
    text: "city",
    dataField: "city",
    sort: true,
    editable: false,
    headerStyle: { backgroundColor: 'whitesmoke'} 
  }
 
];

const defaultSorted = [{
  dataField: 'DC_flag',
  order: 'asc'
}];


//------------pagination property
const pagination = paginationFactory({
  page: 1,
  sizePerPageList: [ {
    text: '5', value: 5
  }, {
    text: '10', value: 10
  },
  {
    text: '25', value: 25
  }, {
    text: '50', value: 50
  } ],
  lastPageText: '>>',
  firstPageText: '<<',
  nextPageText: '>',
  prePageText: '<',
  showTotal: true,
  alwaysShowAllBtns: true,
  // onPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // },
  // onSizePerPageChange: function (page, sizePerPage) {
  //   console.log('page', page);
  //   console.log('sizePerPage', sizePerPage);
  // }
}); 

const { SearchBar, ClearSearchButton } = Search;


  return (
    <>
     {/* ----------------- FOR UPDATE ----------------------------- */}
     <button 
     ref={ref1}
     type="button"
     className="btn btn-primary d-none"
     data-toggle="modal"
     data-target="#exampleModal1"
   >
     Launch demo modal
   </button>

   <div
     className="modal fade"
     id="exampleModal1"
     tabIndex="-1"
     role="dialog"
     aria-labelledby="exampleModalLabel1"
     aria-hidden="true"
   >
     <div className="modal-dialog" role="document">
       <div className="modal-content">
         <div className="modal-header">
           <h5 className="modal-title" id="exampleModalLabel1">
             Edit
           </h5>
           <button
             type="button"
             className="close"
             data-dismiss="modal"
             aria-label="Close"
           >
             <span aria-hidden="true">&times;</span>
           </button>
         </div>
         <div className="modal-body">
         <div className="mb-3">
                  <label className="form-label"><b>Deposite</b></label>
                  <input
                    type="number"
                    className="form-control"
                    id="item_name"
                    name="item_name"
                    onChange={(e) => setdeposite(e.target.value)}
                    required
                    value={deposite}
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Bill No</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={bill_no}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Final Figure</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={final_figure}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Advance</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={advance}
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label"><b>Remaining</b></label>
                  <br></br>
                  <input
                    type="text"
                    className="form-control"
                    value={remaining}
                    disabled
                  />
                </div>
                

         </div>
         <div className="modal-footer">
           <button 
            ref={refclose}
             type="button"
             className="btn btn-secondary"
             data-dismiss="modal"
           >
             Close
           </button>
           <button 
             disabled={deposite.length === 0}
             type="button"
             className="btn btn-primary" 
             onClick={handleclickfirsttray}
           >
             Deliver Bill
           </button>
         </div>
       </div>
     </div>
   </div>

   {/* ----------------- UPDATE END ---------------------------- */}
   
   
   
   {/* Card is started */}
   <div className="card">
  <div className="card-header" style={{backgroundColor:"#80808061"}}>
          <h2 className="opacity-75">
          <u><center>          
                <i className="fa fa-empire" style={{marginRight:"10px"}}></i>
                Booked Rental Records
                <i className="fa fa-empire" style={{marginLeft:"10px"}}></i>
            </center></u>            
            </h2>
  </div>            
  <ul className="list-group list-group-flush">
    <li className="list-group-item" style={{backgroundColor:"#afaaab0d"}}>
{/* Card is over */}
   
{
     <ToolkitProvider
     bootstrap4
     keyField='_id'
     data={contextdeposite.firsttray}
     columns={columns}
     search
     wrapperClasses="table-responsive"
   >
     {
       props => (
         <>
         
          <div style={{textAlign:"right"}}><SearchBar  {...props.searchProps}/> 
        <ClearSearchButton  {...props.searchProps} />
           </div>
           <hr />
           <BootstrapTable
             defaultSorted={defaultSorted}
             pagination={pagination}
             {...props.baseProps}
             noDataIndication= "No data to display"
           />
         </>
       )
     }
   </ToolkitProvider>
   }

    
    {/* <table className="table table-striped">
    <thead>
      <tr>
        <th>D</th> 
        <th>Bill No</th> 
        <th>Name</th>
        <th>Mobile</th>
        <th>Delivery Date</th>
        <th>Return Date</th>
        <th>Item Name</th>
        <th>Item Code</th>
        <th>Item Price</th>
        <th>Total Rs</th>
        <th>Advance Rs</th>
        <th>Remaining Rs</th>
        <th>Deposite Rs</th>
        <th>Branch</th>
        
      </tr>
    </thead>
    <tbody>



       {  
        contextdeposite.firsttray.map((item_data, i) => (
            <tr key={i}>
                 <td> 
                    <i className='btn btn-danger btn-sm' onClick={() => {
                        toggleopenfirsttray(
                          item_data.Deposite,
                          item_data.bill_no,
                          item_data.Final_Figure,
                          item_data.Advance,
                          item_data.Remaining
                        ); 
                      }}>D</i>

                  </td>  
                <td>{item_data.bill_no}</td>
                <td>{item_data.Name}</td>
                <td>{item_data.Mobile}</td>
                <td>{item_data.DC_flag}</td>
                <td>{item_data.repairing_flg}</td>
                <td>{item_data.Item}</td>
                <td>{item_data.Item_Code}</td>
                <td>{item_data.Item_Price}</td>
                <td>{item_data.Final_Figure}</td>
                <td>{item_data.Advance}</td>
                <td>{item_data.Remaining}</td>
                <td>{item_data.Deposite}</td>
                <td>{item_data.city}</td>
            </tr>
        ))
       }
    </tbody>
    </table> */}

  
  {/* Card code is started */}
  </li>
    </ul>
  </div>
  {/* Card code is started */}
    
    </>
  )
}

export default DepositeFirstTray
