import React from 'react'
import DepositeFirstTray from './DepositeFirstTray'
import DepositeSecondTray from './DepositeSecondTray'
import DepositeThirdTray from './DepositeThirdTray'

const deposite = () => {
  return (
      <>
      
      <DepositeFirstTray></DepositeFirstTray>
      <DepositeSecondTray></DepositeSecondTray>
      <DepositeThirdTray></DepositeThirdTray>
      </>
  )
}

export default deposite
